<template>
  <div class="container">
    <!-- 卡片 -->
    <el-card>
      <!-- 面包屑 -->
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>文章管理</el-breadcrumb-item>
        <el-breadcrumb-item>文章列表</el-breadcrumb-item>
      </el-breadcrumb>
      <!-- 搜索和添加 -->
      <el-row :gutter="20" style="margin-top: 15px">
        <el-col :span="8">
          <el-input
            placeholder="请输入内容"
            class="input-with-select"
            clearable
            v-model="SerachText"
            @clear="ClearText"
          >
            <el-button
              slot="append"
              icon="el-icon-search"
              @click="Search_article"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="Add_article">添加文章</el-button>
        </el-col>
      </el-row>
      <!-- 文章信息表格 -->
      <el-table :data="list" border tripe>
        <el-table-column label="标题">
          <template slot-scope="scope">
            <div class="arText">
              {{scope.row.article_Title}}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="描述">
          <template slot-scope="scope">
            <div class="arText">
              {{scope.row.article_describe}}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="封面" width="200px">
          <template slot-scope="scope">
            <div @click="Preview(scope.row)" v-if="scope.row.article_cover">
              <img
                :src="$api.API_SERVER + scope.row.article_cover"
                alt="封面图片"
                height="80px"
              />
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label="分类"
          prop="article_Type"
          width="130px"
        ></el-table-column>
        <el-table-column label="发布时间" width="200px">
          <template slot-scope="scope">
            {{ scope.row.Time | TimeDate }}
          </template>
        </el-table-column>
        <el-table-column
          label="作者"
          prop="article_User"
          width="150px"
        ></el-table-column>
        <el-table-column label="操作" width="180px">
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="EditArticle(scope.row._id)"
              >编辑</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="ArticleRemove(scope.row._id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 实现分页加载 -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="query.pagenum"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="query.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <!-- 封面预览对话框 -->
    <el-dialog title="预览图" :visible.sync="PreViewDialog" width="45%">
      <img
        :src="$api.API_SERVER + PreViewPicUrl"
        alt="预览图"
        class="Preview"
      />
    </el-dialog>
    <!-- 编辑文章信息对话框 -->
    <el-dialog
      title="编辑"
      :visible.sync="EditArticleDialog"
      width="45%"
      @close="Close"
    >
      <el-form
        :model="EditArticleData"
        ref="EditArticleDataRef"
        :rules="EditRules"
      >
        <el-form-item label="标题" prop="article_Title">
          <el-input
            v-model="EditArticleData.article_Title"
            maxlength="50"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="标题" prop="article_describe">
          <el-input
            type="textarea"
            v-model="EditArticleData.article_describe"
            maxlength="100"
            show-word-limit
          ></el-input>
        </el-form-item>
        <el-form-item label="分类" prop="article_Type">
          <el-select v-model="EditArticleData.article_Type" style="width: 100%">
            <el-option
              v-for="(item, index) in TypeList"
              :label="item.name"
              :value="item.name"
              :key="index"
            ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="EditArticleDialog = false">取 消</el-button>
        <el-button type="primary" @click="AddEditArticleDialog"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'Article',
  data() {
    return {
      // 文章搜索数据
      SerachText: '',
      // 文章请求参数
      query: {
        pagesize: 10,
        pagenum: 1,
        UserId: JSON.parse(this.Storage.Session.get('data')).id,
      },
      // 文章信息
      list: [],
      // 文章信息的总条数
      total: 0,
      // 预览图片地址
      PreViewPicUrl: '',
      // 控制预览图窗口
      PreViewDialog: false,
      // 编辑文章对话框
      EditArticleDialog: false,
      // 分类数据
      TypeList: [],
      // 编辑文章信息
      EditArticleData: {},
      // 编辑文章表单验证
      EditRules: {
        article_Title: [
          {
            required: true,
            message: '请输入标题',
            trigger: 'blur',
          },
          {
            max: 100,
            message: '长度不能超过100个字符',
            trigger: 'blur',
          },
        ],
        article_describe: [
          {
            required: true,
            message: '请输入描述信息',
            trigger: 'blur',
          },
          {
            max: 100,
            message: '长度不能超过100个字符',
            trigger: 'blur',
          },
        ],
        article_Type: [
          {
            required: true,
            message: '请选择分类',
            trigger: 'blur',
          },
        ],
      },
    }
  },
  created() {
    this.ObtainArticle()
    this.GetTypelist()
  },
  methods: {
    // 获取文章列表
    ObtainArticle() {
      this.$http
        .get(this.$api.API_SERVER_OBTAIN_ARTICLE, { params: this.query })
        .then((res) => {
          if (res.data.status !== 200) {
            return this.$message.error(res.data.msg)
          }
          this.list = res.data.data
          this.total = res.data.total
        })
    },
    //   获取分类数据
    GetTypelist() {
      this.$http.get(this.$api.API_SERVER_ARTICLE_TYPE_LIST).then((res) => {
        if (res.data.status != 200) {
          return this.$message.error('获取分类失败')
        }
        this.TypeList = res.data.data.slice(1, 5)
        this.$store.commit('GetType', res.data.data.slice(1, 5))
      })
    },
    // 路由跳转到添加文章页
    Add_article() {
      this.$router.push('/Addarticle')
    },
    // 封面预览图
    Preview(val) {
      this.PreViewPicUrl = val.article_cover
      this.PreViewDialog = true
    },
    // 每一个分页的总条数
    handleSizeChange(Size) {
      this.query.pagesize = Size
      this.ObtainArticle()
    },
    // 控制页码值
    handleCurrentChange(Num) {
      this.query.pagenum = Num
      this.ObtainArticle()
    },
    // 用户数据编辑
    EditArticle(id) {
      const data = this.list.filter((item) => {
        return item._id == id
      })
      this.EditArticleData = data[0]
      this.TypeList = JSON.parse(this.Storage.Session.get('Type'))
      this.EditArticleDialog = true
    },
    // 提交编辑后的用户数据
    AddEditArticleDialog() {
      this.$refs.EditArticleDataRef.validate((valid) => {
        if(!valid){
          return this.$message.error('请输入文章介绍信息')
        }
        const params = {
          title: this.EditArticleData.article_Title,
          describe:this.EditArticleData.article_describe,
          Type: this.EditArticleData.article_Type,
        }
        this.$http
          .put(
            this.$api.API_SERVER_EDIT_ARTICLE + this.EditArticleData._id,
            params
          )
          .then((res) => {
            if (res.data.status != 201) {
              return this.$message.error(res.data.msg)
            }
            this.EditArticleDialog = false
            this.$message.success(res.data.msg)
            this.ObtainArticle()
          })
      })
    },
    // 对话框关闭的回调函数
    Close() {
      this.$refs.EditArticleDataRef.resetFields()
    },
    // 删除文章信息
    ArticleRemove(id) {
      this.$confirm('此操作将永久删除该文章, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
      })
        .then(() => {
          this.$http
            .delete(this.$api.API_SERVER_REMOVE_ARTICLE + id)
            .then((res) => {
              if (res.data.status !== 200) {
                return this.$message.error(res.data.msg)
              }
              this.$message.success(res.data.msg)
              this.ObtainArticle()
            })
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除',
          })
        })
    },
    Search_article() {
      const params = {
        id: this.query.UserId,
        Text: this.SerachText,
      }
      this.$http
        .get(this.$api.API_SERVER_SEARCH_ARTICLE, { params: params })
        .then((res) => {
          if (res.data.status != 200) {
            this.list = res.data.data
            return this.$message.error(res.data.msg)
          }
          this.list = res.data.data
        })
    },
    // 请求搜索框数据就重新加载文章数据
    ClearText() {
      this.ObtainArticle()
    },
  },
}
</script>
<style lang="less" scoped>
.Preview {
  width: 100%;
  height: 100%;
}
.arText{
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  line-clamp: 2;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
</style>